<script>
	import timeV from "@/components/addorder/order-time.vue"
	export default {

		components: {
			timeV
		},
		data() {
			return {
				system_top: 44,
				scrollHeight: 0,
				adsTypeArray: [],
				adsTypeIndex: 0,
				isEdit: true,
				timer: null,
				timeCount: 0,
				changeIndex: null,
				goodsArray: [{
					num: 1
				}, {
					num: 1
				}, {
					num: 1
				}],
				isShowTime: false,
				timeValue: {
					'time': '请选择',
					'servetime': 0,
					'tian': ''
				},
				iscanju: true
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			const res = uni.getSystemInfoSync();
			if (parseInt(res.safeArea.top) > this.system_top) {
				this.system_top = parseInt(res.safeArea.top)
			}
			this.scrollHeight = res.windowHeight - this.system_top - 40 - 60 - 100
			this.adsTypeArray = [this.i18n.meishi.waimaipeisong, this.i18n.meishi.daodianziti]
		},
		onLoad(option) {
			var that = this
			uni.$on('timeSelcted', (e) => {
				that.isShowTime = 0
				if (e != 0) {
					// if(that.adsTypeIndex == 0){
					that.timeValue = e
					// }
				}
			})
		},
		onUnload() {
			// 移除监听事件  
			uni.$off('timeSelcted');
		},
		methods: {
			tabbarClick(index) {
				this.adsTypeIndex = index
			},
			editPhone() {
				this.isEdit = false
			},
			doSure() {
				this.isEdit = true
			},
			doBack() {
				uni.navigateBack({

				})
			},
			changeNums(index, item) {
				if (this.timer) {
					clearInterval(this.timer)
					this.timer = null
				}
				this.changeIndex = index
				this.timeCount = 5
				this.timer = setInterval(() => {
					this.timeCount--
					if (this.timeCount == 0) {
						clearInterval(this.timer)
						this.timer = null
					}
				}, 1000)
			},
			jiashuliang(item) {
				this.timeCount = 5
				item.num = parseInt(item.num) + 1
			},
			jianshuliang(item) {
				this.timeCount = 5
				if (parseInt(item.num) > 1) {
					item.num = parseInt(item.num) - 1
				}
			},
			showTimeV(index) {
				this.isShowTime = true
			},
			docanjuSelected() {
				this.iscanju = !this.iscanju
			},
			next(){
				uni.navigateTo({
					url:'./next'
				})
			}
		}
	}
</script>

